$font-family-default: 'Manrope';
$font-size-base: 16px;

@font-face {
	font-family: 'Manrope';
	src: url('./Manrope-Bold.eot');
	src: url('./Manrope-Bold.eot?#iefix') format('embedded-opentype'), url('./Manrope-Bold.woff2') format('woff2'),
		url('./Manrope-Bold.woff') format('woff');
	font-weight: bold;
	font-style: normal;
	font-display: swap;
}

